:root {
  --font-family: 'Chillax', sans-serif;
  --second-family: 'Bitter Rose', sans-serif;
  --third-family: 'Cabinet Grotesk', sans-serif;

  --text-default: #fff;
  --text-muted: rgba(255, 255, 255, 0.8);
  --text-primary: #face8d;
  --text-inverse: #081212;
  --background-default: #050505;
  --background-primary: #f8d49e;
  --border-default: rgba(255, 255, 255, 0.1);
  --icon-default: #fff;
  --icon-primary: #f8d49e;
  --icon-inverse: #081212;

  --laptop-size: 1199px;
  --tablet-size: 959px;
  --mobile-size: 599px;
}
